						label="医生姓名">
						<template slot-scope="scope">
							{{scope.row.yishengxingming}}
						</template>
					</el-table-column>
					<el-table-column :resizable='true' :sortable='true'  
						prop="zixunriqi"
						label="咨询日期">
						<template slot-scope="scope">
							{{scope.row.zixunriqi}}
						</template>
					</el-table-column>
					<el-table-column :resizable='true' :sortable='true'  
						prop="zixunneirong"
						label="咨询内容">
